import App from '../components/App';
import Home from '../components/home';
import Info from '../components/info';
import About from '../components/about';

const routes = {
  path: '/CHUI',
  component: App,
  indexRoute: {
    component: Home
  },
  childRoutes: [
    {path: '/CHUI/home', component: Home},
    {path: '/CHUI/info', component: Info},
    {path: '/CHUI/about', component: About}
  ]
}
export default routes;


